.u-time-picker {
  display: inline-block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
}
.u-time-picker i.uf {
  position: absolute;
  right: 0;
  top: 4px;
  opacity: 0.2;
  cursor: pointer;
}
.u-time-picker * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.u-time-picker .u-time-picker-input {
  width: 100%;
  position: relative;
  display: inline-block;
  padding: 4px 7px;
  height: 32px;
  cursor: text;
  font-size: 14px;
  line-height: 32;
  color: #212121;
  background-color: #fff;
  background-image: none;
  border: 1px solid #a5adba;
  border-radius: 3px;
