@import "../../static/font/iconfont.css";
.ui-nc-header-warp {
  color: #e3e7ed;
}
.syncTreeCom {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 100%;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  position: relative;
}
.syncTreeCom ul,
.syncTreeCom li,
.syncTreeCom span,
.syncTreeCom div {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}
.syncTreeCom .noDrag .title-con:first-child {
  cursor: not-allowed;
}
.syncTreeCom .icon-control {
  color: #757f8c;
  font-size: 12px;
}
.syncTreeCom .u-form-control {
  font-size: 13px;
}
.syncTreeCom .u-form-control:focus {
  outline: 0;
}
.syncTreeCom .NC_syncTreeSearch {
  margin-bottom: 10px;
  position: relative;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
}
.syncTreeCom .NC_syncTreeSearch .u-form-control::-webkit-input-placeholder {
  color: #888888;
}
.syncTreeCom .NC_syncTreeSearch .u-form-control::-moz-placeholder {
  color: #888888;
}
.syncTreeCom .NC_syncTreeSearch .u-form-control::-ms-input-placeholder {
  color: #888888;
}
.syncTreeCom .NC_syncTreeSearch .u-form-control::placeholder {
  color: #888888;
}
.syncTreeCom .NC_syncTreeSearch:hover .syncTreeSearchIcon.qingkong {
  display: block;
  color: #d2d2d2;
}
.syncTreeCom .NC_syncTreeSearch .syncTreeSearchIcon {
  position: absolute;
  right: 10px;
  top: 7px;
  color: #878b94;
  cursor: pointer;
}
.syncTreeCom .NC_syncTreeSearch .syncTreeSearchIcon.qingkong {
  display: none;
  right: 30px;
  color: #d2d2d2;
}
.syncTreeCom .synctree-area {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  overflow: auto;
  width: 100%;
}
.syncTreeCom .synctree-area .u-tree-checkbox-disabled {
  display: none;
}
.syncTreeCom .synctree-area .u-tree .node-item:only-child > ul::before {
  border-left: none;
}
.syncTreeCom .synctree-area .u-tree-child-tree-open {
  position: relative;
  padding-left: 19px;
}
.syncTreeCom .synctree-area .u-tree-child-tree-open > li:first-child::after {
  position: absolute;
  left: 8px;
  top: -17px;
  content: "";
  display: inline-block;
  width: 1px;
  height: 33px;
  /* background: red; */
  border-left: 1px dashed #d0d0d0;
}
.syncTreeCom .synctree-area .u-tree-child-tree-open::before {
  position: absolute;
  left: 8px;
  top: -12px;
  content: "";
  display: inline-block;
  width: 1px;
  height: 100%;
  /* background: red; */
  border-left: 1px dashed #d0d0d0;
}
.syncTreeCom .synctree-area .node-item + .node-item::after {
  position: absolute;
  left: 8px;
  top: -15px;
  content: "";
  display: inline-block;
  width: 1px;
  height: 32px;
  /* background: red; */
  border-left: 1px dashed #d0d0d0;
}
.syncTreeCom .synctree-area .node-item {
  position: relative;
  z-index: 2;
}
.syncTreeCom .synctree-area .node-item:last-child > ul::before {
  border: none;
}
.syncTreeCom .synctree-area .node-item .u-tree-switcher {
  position: relative;
  z-index: 2;
}
.syncTreeCom .synctree-area .node-item .u-tree-switcher::before {
  position: absolute;
  left: 11px;
  top: 10px;
  content: "";
  display: inline-block;
  width: 10px;
  border-top: 1px dashed #d0d0d0;
}
.syncTreeCom .synctree-area .node-item .u-tree-switcher .icon {
  position: relative;
  background: transparent;
}
.syncTreeCom .synctree-area .node-item .u-tree-checkbox .u-tree-checkbox-inner {
  left: 5px;
}
.syncTreeCom .synctree-area .node-item .u-tree-checkbox-checked .u-tree-checkbox-inner {
  background: #e14c46;
  border-color: #e14c46;
}
.syncTreeCom .synctree-area .node-item .u-tree-node-content-wrapper {
  margin-left: 1px;
}
.syncTreeCom .synctree-area .isexpend::after {
  position: absolute;
  left: 8px;
  top: -15px;
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  border-left: none;
}
.syncTreeCom .NC_iconBox {
  opacity: 0;
}
.syncTreeCom .NC_iconBox .icon:first-child {
  margin-left: 20px;
}
.syncTreeCom .title-con:hover .NC_iconBox {
  opacity: 1;
}
.syncTreeCom .edit-icon,
.syncTreeCom .add-icon,
.syncTreeCom .delete-icon,
.syncTreeCom .customer-icon {
  line-height: 30px;
  margin-right: 5px;
  font-size: 14px;
}
.syncTreeCom .u-tree {
  padding: 0;
}
.syncTreeCom .title-middle {
  line-height: 30px;
  margin-left: 6px;
}
.syncTreeCom .u-tree li {
  line-height: 30px;
}
.syncTreeCom .u-tree-searchable-filter {
  color: #e14c46;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.syncTreeCom .addRoot {
  color: #f34b4b;
  cursor: pointer;
  width: 80px;
  height: 30px;
  border-radius: 4px;
  line-height: 30px;
  background: #f34b4b;
  text-align: center;
  margin-bottom: 5px;
  font-size: 12px;
}
.syncTreeCom .active-nodisable {
  background: #ebebf2;
  width: 100%;
  color: #e14c46;
}
.syncTreeCom .active-nodisable .title-middle {
  color: #e14c46;
}
.syncTreeCom .title-con {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  overflow: hidden;
  min-width: 150px;
}
.syncTreeCom .title-con .title-single {
  display: inline-block;
  vertical-align: bottom;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.syncTreeCom .title-con .icon {
  font-size: 15px;
}
.syncTreeCom .title-con .NC_iconBox .icon {
  font-size: 14px;
}
.syncTreeCom .title-con input {
  float: left;
}
.syncTreeCom .node_right_opt {
  position: absolute;
  left: 120px;
  width: 30px;
  height: 100%;
}
.tree-swich {
  color: #757f8c;
}
.tree-wenjian {
  color: #f2b224;
}
.tree-dian {
  content: ".";
  display: inline-block;
  width: 5px;
  height: 5px;
  background: #555555;
  border-radius: 50%;
  margin-right: 5px;
  margin-left: 5px;
  vertical-align: middle;
}
.u-tree li a.u-tree-node-selected.u-tree-node-content-wrapper {
  background: #ebebf2;
}
.nc-tree.u-tree li a.u-tree-node-content-wrapper {
  padding-top: 0;
  padding-bottom: 0;
}
.disabledSelected > .u-tree-node-content-wrapper {
  background: #ebebf2;
}
.disabledSelected > .u-tree-node-content-wrapper .title-middle {
  color: #e14c46;
}
.stop-node-dian {
  background-color: #c2c2c2;
}
.stop-node {
  color: #c2c2c2;
}
