.searchPlanBoxHeight {
  max-height: 90px;
}
.NC_searchPlanBox {
  overflow: hidden;
  height: auto;
  padding-bottom: 4px;
  background: #f9f9f9;
  -webkit-box-shadow: 0px 1px 0px #ececec;
          box-shadow: 0px 1px 0px #ececec;
  border: 1px solid #d0d0d0;
}
.NC_searchPlanBox .NC_searchPlanName {
  width: 10%;
  height: 100%;
  overflow: hidden;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  float: left;
  padding-top: 10px;
  border-radius: 4px;
  padding-left: 8px;
  font-size: 12px;
  color: #555555;
}
.NC_searchPlanBox .NC_searchPlanMain {
  float: left;
  width: 85%;
  padding-top: 5px;
}
.NC_searchPlanBox .NC_searchPlanMain .searchChild {
  margin-bottom: 10px;
}
.NC_searchPlanBox .NC_searchPlanBtn {
  float: right;
  height: 100%;
  width: 5%;
}
.NC_searchPlanBox .NC_searchPlanBtn .closeSearchPlan {
  float: right;
  margin-right: 5px;
  font-size: 28px;
  text-align: center;
  line-height: 30px;
  color: #757f8c;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.NC_searchPlanBox .NC_searchPlanBtn .moreSearchPlan {
  float: right;
  position: absolute;
  bottom: 10px;
  color: #007ace;
  font-size: 12px;
  right: 10px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.NC_searchAdvComBox {
  margin: 0;
  padding: 0;
  height: auto;
}
.NC_searchAdvComBox .u-tree-switcher {
  margin-top: 9px !important;
}
.NC_searchAdvComBox .u-tree-switcher .icon-control {
  font-size: 14px;
  color: #757f8c;
}
.NC_searchAdvComBox .u-tree li a {
  display: inline-block;
  width: 96%;
}
.NC_searchAdvCom {
  width: 100%;
  min-height: 45px;
  background-color: #f4f5f7;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.NC_searchAdvCom .nc-select .u-select-selection {
  height: auto;
  min-height: 30px;
  margin-right: 0px;
  margin-bottom: 10px;
}
.NC_searchAdvCom .checkbox_switchtype {
  line-height: 0;
}
.NC_searchAdvCom .checkbox_switchtype .u-checkbox-label::before {
  top: 0;
}
.NC_searchAdvCom .checkbox_switchtype .u-checkbox-label::after {
  top: 0;
}
.NC_searchAdvCom .checkboxtype .checkbox {
  line-height: 28px;
}
.NC_searchAdvCom .checkboxtype .checkbox .u-checkbox {
  margin-right: 5px;
}
.NC_searchAdvCom .mustFillIn {
  float: left;
  color: #ff0000;
  margin-top: 10px;
  font-size: 16px;
  margin-left: 2px;
  position: absolute;
}
.NC_searchAdvCom .searchAdvCom_name {
  line-height: 45px;
  width: 130px;
  padding-left: 10px;
  color: #474d54;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.NC_searchAdvCom .searchAdvCom_select {
  margin-top: 8px;
  width: 60px;
}
.NC_searchAdvCom .searchAdvCom_select .nc-select .u-select-selection {
  padding: 0;
}
.NC_searchAdvCom .searchAdvCom_select .option-label {
  display: inline-block;
  font-size: 13px;
  text-align: center;
  padding-top: 8px;
}
.NC_searchAdvCom .searchAdvCom_com {
  margin-top: 8px;
  margin-left: 10px;
  width: 146px;
}
.NC_searchAdvCom .searchAdvCom_com .u-select-selection {
  width: 100%;
}
.NC_searchAdvCom .searchAdvCom_com .calendar-picker {
  width: 100%;
}
.NC_searchAdvCom .searchAdvCom_com .u-form-control {
  width: 100%;
  font-size: 12px;
  border-radius: 4px;
}
.NC_searchAdvCom .lightapp-component-form {
  padding: 0;
}
.NC_searchAdvCom .u-row {
  margin: 0;
  min-height: 40px;
  height: 45px;
}
.NC_searchAdvCom .formControl {
  margin: 0 !important;
  padding: 0 !important;
  margin-top: 8px !important;
  margin-left: 10px !important;
  width: 380px;
}
.NC_searchAdvCom .formControl .u-form-control {
  width: 380px !important;
}
.NC_searchAdvCom .searchAdvCom_remove {
  line-height: 45px;
  color: #919191;
  cursor: pointer;
}
