@import "../../static/font/iconfont.css";
.ui-nc-header-warp {
  color: #e3e7ed;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.cf:before,
.cf:after {
  display: table;
  content: " ";
}
.cf:after {
  clear: both;
}
.animated {
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.animated.infinite {
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}
.lightapp-component-ncviewer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 20px 20px 100px;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: rgba(0, 0, 0, 0.7);
  z-index: 10000;
}
.lightapp-component-ncviewer img {
  max-width: 100%;
  max-height: 100%;
}
.lightapp-component-ncviewer figure {
  margin: 0;
}
.lightapp-component-ncviewer .ncviewer-image {
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.lightapp-component-ncviewer .ncviewer-close {
  position: absolute;
  font-size: 20px;
  line-height: 114px;
  padding-left: 19px;
  text-align: left;
  top: -40px;
  right: -40px;
  width: 80px;
  height: 80px;
  cursor: pointer;
  color: #fff;
  border-radius: 50%;
  background-color: #000;
}
.lightapp-component-ncviewer .ncviewer-footer {
  position: absolute;
  width: 100%;
  height: 70px;
  bottom: 20px;
  left: 0;
  color: #fff;
  text-align: center;
}
.lightapp-component-ncviewer .ncviewer-footer .ncviewer-title {
  line-height: 30px;
  font-weight: 600;
}
.lightapp-component-ncviewer .ncviewer-footer .ncviewer-toolbar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  height: 40px;
  line-height: 40px;
  width: 120px;
  margin: 0 auto;
}
.lightapp-component-ncviewer .ncviewer-footer .ncviewer-opr {
  height: 40px;
  width: 30px;
  cursor: pointer;
}
.lightapp-component-ncviewer .ncviewer-footer .ncviewer-opr.is-disabled {
  opacity: 0.2;
  cursor: not-allowed;
}
.lightapp-component-ncviewer .ncviewer-footer .ncviewer-opr .uf {
  font-size: 24px;
}
