/*  Navlayout  */
.u-popover {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1540;
  display: none;
  padding: 1px;
  font-style: normal;
  font-weight: 400;
  letter-spacing: normal;
  line-break: auto;
  line-height: 1.42857143;
  text-align: left;
  text-decoration: none;
  text-shadow: none;
  text-transform: none;
  white-space: normal;
  word-break: normal;
  word-spacing: normal;
  word-wrap: normal;
  font-size: 14px;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ccc;
  border-radius: 3px;
  -webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
          box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}
.u-popover > .arrow,
.u-popover > .arrow:after {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
}
.u-popover > .arrow {
  border-width: 10px;
}
.u-popover > .arrow:after {
  border-width: 10px;
  content: "";
}
.u-popover.top,
.u-popover.top-right,
.u-popover.top-left {
  margin-top: -10px;
}
.u-popover.top .arrow,
.u-popover.top-right .arrow,
.u-popover.top-left .arrow {
  left: 50%;
  margin-left: -11px;
  border-bottom-width: 0;
  border-top-color: #999;
  bottom: -11px;
}
.u-popover.top .arrow:after,
.u-popover.top-right .arrow:after,
.u-popover.top-left .arrow:after {
  content: " ";
  bottom: 1px;
  margin-left: -10px;
  border-bottom-width: 0;
  border-top-color: #fff;
}
.u-popover.top-right .arrow {
  left: auto;
  right: 20px;
}
.u-popover.top-left .arrow {
  left: 20px;
}
.u-popover.right,
.u-popover.right-top,
.u-popover.right-bottom {
  margin-left: 10px;
}
.u-popover.right .arrow,
