.u-popover.right-bottom .arrow:after {
  content: " ";
  left: 1px;
  bottom: -10px;
  border-left-width: 0;
  border-right-color: #fff;
}
.u-popover.right-top .arrow {
  top: 20px;
}
.u-popover.right-bottom .arrow {
  top: auto;
