.rejectModal .u-modal-content {
  width: 520px;
  height: 252px;
}
.rejectModal .u-modal-header {
  border-bottom: 1px solid #e9e9e9 !important;
}
.rejectModal .modalTitle {
  font-size: 13px;
  color: #666;
}
.rejectModal .closeBtn {
  float: right;
  font-size: 30px;
  margin-top: -11px;
  color: #bdbdbd;
  cursor: pointer;
}
.rejectModal .mainName {
  display: inline-block;
  vertical-align: top;
  margin: 10px 10px 0 30px;
  font-size: 13px;
  color: #666;
}
.rejectModal .mainBox {
  width: 330px;
  height: 84px;
  border-color: #d9d9d9;
  resize: none;
  border-radius: 4px;
}
.rejectModal .beSureBtn {
  width: 102px;
  height: 36px;
  font-size: 13px;
  border-radius: 4px;
}
.rejectModal .cancelBtn {
  width: 102px;
  height: 36px;
  background-color: #eee;
  color: #333;
  font-size: 13px;
  border-radius: 4px;
}
.rejectModal .mainLength {
  position: absolute;
  bottom: 22px;
  right: 72px;
  font-size: 12px;
  color: #999;
}
.rejectModal .u-modal-content .u-modal-footer {
  padding: 15px 20px 0 0;
}
.rejectModal .u-modal-content .u-modal-body {
  padding: 30px 15px 15px 15px;
}
