.nc-popconfirm.u-popconfirm > .arrow,
.nc-popconfirm.u-popconfirm > .arrow:after {
  border-color: transparent;
}
.nc-popconfirm.u-popconfirm.bottom > .arrow {
  border-bottom-color: #e0e0e0;
}
.nc-popconfirm.u-popconfirm.bottom > .arrow:after {
  border-bottom-color: #fff;
}
.nc-popconfirm.u-popconfirm.top > .arrow {
  border-top-color: #e0e0e0;
}
.nc-popconfirm.u-popconfirm.top > .arrow:after {
  border-top-color: #fff;
}
.nc-popconfirm.u-popconfirm {
  font-size: 12px;
  width: 212px;
  z-index: 310;
  background-color: #fff;
  border: 1px solid #e0e0e0;
}
.nc-popconfirm.u-popconfirm .u-popconfirm-content {
  color: #111;
}
.nc-popconfirm.u-popconfirm .icon-tixing {
  color: #ff8b00;
  font-size: 13px;
  position: relative;
  top: 1px;
}
.nc-popconfirm.u-popconfirm .u-popconfirm-confirm {
  text-align: right;
}
.nc-popconfirm.u-popconfirm .u-popconfirm-confirm .u-button {
  min-width: 44px;
  height: 22px;
  border-radius: 3px;
  font-size: 12px;
  line-height: 12px;
  margin: 5px;
  will-change: auto;
}
.nc-popconfirm.u-popconfirm .u-popconfirm-confirm .u-button.u-button-primary {
  background: #e14c46;
  color: #fff;
  border: 1px solid #e14c46;
}
.nc-popconfirm.u-popconfirm .u-popconfirm-confirm .u-button.u-button-primary:hover {
  background: #d7332c;
  border: 1px solid #d7332c;
}
.nc-popconfirm.u-popconfirm .u-popconfirm-confirm .u-button.u-button-primary:active {
  background: #b2332e;
  border: 1px solid #b2332e;
}
.nc-popconfirm.u-popconfirm .u-popconfirm-confirm .u-button.u-button-border {
  float: right;
  margin-right: 12px;
  background: #fff;
  color: #111;
  border-color: #fff;
}
.nc-popconfirm.u-popconfirm .u-popconfirm-confirm .u-button.u-button-border:hover {
  background: #fff;
  color: #e14c46;
  border-color: #e14c46;
}
.popconfirm-hotkeys-wrapper {
  display: inline-block;
}
.u-tooltip.fade.inverse.model-helper-overlay .tooltip-inner {
  padding: 5px 10px;
  font-weight: 600;
}
