.u-dropdown {
  position: absolute;
  left: -9999px;
  top: -9999px;
  z-index: 1200;
  display: block;
  font-size: 12px;
  font-weight: normal;
  line-height: 1.5;
}
.u-dropdown-hidden {
  display: none;
}
.u-dropdown-menu {
  outline: none;
  position: relative;
  list-style-type: none;
  padding: 0;
  margin: 5px 0 0 0;
  text-align: left;
  background-color: #fff;
  border-radius: 3px;
  -webkit-box-shadow: 0 1px 5px #a5adba;
          box-shadow: 0 1px 5px #a5adba;
  background-clip: padding-box;
}
.u-dropdown-menu > li {
  margin: 0;
  padding: 0;
}
.u-dropdown-menu-submenu {
  position: relative;
}
.u-dropdown-menu-submenu-title {
  margin: 0;
  position: relative;
  display: block;
  white-space: nowrap;
  padding: 0px 16px 0 16px;
  line-height: 42px;
  height: 42px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.u-dropdown-menu-submenu-title:hover {
  background: #ebecf0;
}
.u-dropdown-menu-submenu-vertical > .u-dropdown-menu-submenu-title:after {
  -webkit-transform: rotate(270deg) scale(0.75);
          transform: rotate(270deg) scale(0.75);
  font-family: "uf";
  font-style: normal;
  vertical-align: baseline;
  text-align: center;
  text-transform: none;
  text-rendering: auto;
  position: absolute;
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  content: "\e609";
  right: 16px;
  top: 0;
  display: inline-block;
  font-size: 8px \9;
  /* transform: scale(0.66667) rotate(0deg); */
  -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(sizingMethod='auto expand', M11=1, M12=0, M21=0, M22=1)";
  zoom: 1;
}
.u-dropdown-menu-hidden {
  display: none;
}
.u-dropdown-menu:before {
  content: "";
  position: absolute;
  top: -4px;
  left: 0;
  width: 100%;
  height: 4px;
  background: white;
  background: rgba(255, 255, 255, 0.01);
}
.u-dropdown-menu-item-group-title {
  color: #909090;
  font-size: 12px;
  line-height: 1.5;
  padding: 8px 16px;
}
.u-dropdown-menu > .u-dropdown-menu-item {
  position: relative;
  display: block;
  padding: 0px 16px 0 16px;
  line-height: 42px;
  height: 42px;
  overflow: hidden;
  text-overflow: ellipsis;
  clear: both;
  font-weight: normal;
  white-space: nowrap;
}
.u-dropdown-menu > .u-dropdown-menu-item:hover,
.u-dropdown-menu > .u-dropdown-menu-item-active,
.u-dropdown-menu > .u-dropdown-menu-item-selected {
  background-color: #ebecf0;
  cursor: pointer;
}
.u-dropdown-menu > .u-dropdown-menu-item-selected {
  color: #f53c32;
  background-color: #fff7e7;
  position: relative;
}
.u-dropdown-menu > .u-dropdown-menu-item-selected:hover {
  background-color: #fff7e7;
}
.u-dropdown-menu > .u-dropdown-menu-item-disabled {
  color: #909090;
  cursor: not-allowed;
}
.u-dropdown-menu > .u-dropdown-menu-item-disabled:hover {
  color: #909090;
  background-color: #fff;
  cursor: not-allowed;
}
.u-dropdown-menu > .u-dropdown-menu-item:last-child {
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
}
.u-dropdown-menu > .u-dropdown-menu-item:first-child {
  margin-top: 5px;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}
.u-dropdown-menu > .u-dropdown-menu-item-divider {
  height: 1px;
  margin: 1px 0;
  overflow: hidden;
  background-color: #e0e0e0;
  line-height: 0;
}
.u-dropdown-menu-item-group-list {
  padding: 0;
}
.u-dropdown-menu-item-group-list > .u-dropdown-menu-item {
  padding: 0px 16px 0 16px;
  line-height: 42px;
  height: 42px;
  overflow: hidden;
