.reject-detail .name {
  position: relative;
  top: -15px;
  display: inline-block;
  color: #474d54;
  font-size: 14px;
  font-weight: 600;
}
.reject-detail .u-modal-content .u-modal-header {
  padding: 0 16px;
}
.reject-detail .nc-tabs {
  width: 80%;
  display: inline-block;
  background-color: transparent;
  margin-right: 10px;
}
.reject-detail .u-tabs-top .u-tabs-bar {
  margin-bottom: 0;
}
.reject-detail .u-tabs.u-tabs-top.u-tabs-simple.lightapp-component-tabs .u-tabs-bar,
.reject-detail .u-tabs.u-tabs-top.u-tabs-simple.lightapp-component-tabs {
  background-color: transparent;
}
.reject-detail .detail {
  width: 100%;
  max-width: none !important;
}
.reject-detail .detail .img {
  text-align: center;
}
.reject-detail .detail .hide-detail-img {
  display: none;
}
.reject-detail .detail .detail-btn {
  position: absolute;
  top: 60px;
  right: 20px;
  z-index: 1;
}
.reject-detail .detail .reject-dagre.dagre-area {
  width: 100%;
}
.reject-detail .detail .reject-dagre.dagre-area .dagre-extract {
  position: absolute;
  display: none;
  top: 15px;
  right: 100px;
}
