.u-transfer {
  position: relative;
  line-height: 1.5;
}
.u-transfer-list {
  font-size: 12px;
  border: 1px solid #d9d9d9;
  display: inline-block;
  border-radius: 4px;
  vertical-align: middle;
  position: relative;
  width: 180px;
  height: 200px;
  padding-top: 33px;
}
.u-transfer-list-with-footer {
  padding-bottom: 33px;
}
.u-transfer-list-search-action {
  color: #ccc;
  position: absolute;
  top: 2px;
  right: 2px;
  width: 32px;
  height: 32px;
  line-height: 32px;
  text-align: center;
  font-size: 14px;
  text-decoration: none;
}
.u-transfer-list-search-action .uf {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  font-size: 12px;
  color: #ccc;
}
.u-transfer-list-search-action .uf:hover {
  color: rgba(0, 0, 0, 0.43);
}
.u-transfer-list-header {
  padding: 7px 15px;
  border-radius: 4px 4px 0 0;
  background: #fff;
  color: rgba(0, 0, 0, 0.65);
  border-bottom: 1px solid #e9e9e9;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.u-transfer-list-header-title {
  position: absolute;
  right: 15px;
}
.u-transfer-list-body {
  font-size: 12px;
  position: relative;
  height: 100%;
}
.u-transfer-list-body-search-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  height: 28px;
  padding: 4px;
  width: 100%;
}
.u-transfer-list-body-with-search {
  padding-top: 34px;
}
.u-transfer-list-content {
  height: 100%;
  overflow: auto;
  margin: 0;
  padding: 0;
}
.u-transfer-list-content-item {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding: 7px 15px;
  min-height: 32px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.u-transfer-list-content-item:not(.u-transfer-list-content-item-disabled):hover {
  cursor: pointer;
  background-color: #ecf6fd;
}
.u-transfer-list-content-item-disabled {
  cursor: not-allowed;
  color: rgba(0, 0, 0, 0.25);
}
.u-transfer-list-content-item-highlight-enter {
  -webkit-animation: transferHighlightIn 1s ease;
          animation: transferHighlightIn 1s ease;
  -webkit-transition: none;
  transition: none;
}
.u-transfer-list-body-not-found {
  padding-top: 0;
  color: #ccc;
  text-align: center;
  display: none;
  position: absolute;
  top: 50%;
  width: 100%;
  margin-top: -10px;
}
.u-transfer-list-content:empty + .u-transfer-list-body-not-found {
  display: block;
}
.u-transfer-list-footer {
  border-top: 1px solid #e9e9e9;
  border-radius: 0 0 4px 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.u-transfer-operation {
  display: inline-block;
  overflow: hidden;
  margin: 0 8px;
  vertical-align: middle;
}
.u-transfer-operation button {
  display: block;
}
.u-transfer-operation button:first-child {
  margin-bottom: 4px;
}
.u-transfer-operation button .uf {
  font-size: 12px;
}
.u-transfer .u-checkbox + span,
.u-transfer .u-checkbox-wrapper + span {
  padding-left: 8px;
  padding-right: 8px;
}
.u-transfer .u-checkbox-checked .u-checkbox-label,
.u-transfer .u-checkbox-indeterminate .u-checkbox-label {
  background-color: #108ee9;
  border-color: #108ee9;
}
.u-transfer .u-checkbox.u-checkbox-indeterminate .u-checkbox-label:after {
  color: #fff;
  content: "\e6ce";
  line-height: 18px;
  font-size: 14px;
}
.u-transfer .u-checkbox .u-checkbox-label:before,
.u-transfer .u-checkbox .u-checkbox-label:after {
  top: 0;
}
.u-transfer .u-checkbox.u-checkbox-indeterminate .u-checkbox-label:before {
  -webkit-box-shadow: inset 0 0 0 10px #1e88e5;
          box-shadow: inset 0 0 0 10px #1e88e5;
  border-color: #1e88e5;
}
.u-transfer-operation .u-button-sm {
  padding: 0;
  font-size: 1.2rem;
  border: 1px solid;
  color: #6d6b6b;
  min-width: 3rem;
}
.u-transfer-operation .u-button-sm {
  background: #1e88e5;
  color: #fff;
  border-color: #1e88e5;
}
.u-transfer-operation .u-button-sm:hover {
  background: #1e88e5;
  border-color: #1e88e5;
}
.u-transfer-operation .u-button-sm[disabled] {
  background: #fff;
  color: #9a9898;
  border-color: #e0e0e0;
}
.u-button.disabled,
.u-button[disabled]:hover {
  background: #fff;
  color: #9a9898;
  border-color: #e0e0e0;
}
.u-transfer-list-search {
  font-size: 1.2rem;
  border-color: #d9d9d9;
  height: 26px;
}
@-webkit-keyframes transferHighlightIn {
  0% {
    background: #1e88e5;
  }
  100% {
    background: transparent;
  }
}
@keyframes transferHighlightIn {
  0% {
    background: #1e88e5;
  }
  100% {
    background: transparent;
  }
}
