.asyncTreeCom {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 100%;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  position: relative;
}
.asyncTreeCom ul,
.asyncTreeCom li,
.asyncTreeCom span,
.asyncTreeCom div {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}
.asyncTreeCom .noDrag .title-con:first-child {
  cursor: not-allowed;
}
.asyncTreeCom .icon-control {
  color: #757f8c;
  font-size: 12px;
}
.asyncTreeCom .u-form-control {
  border-color: #d9d9d9;
  font-size: 13px;
}
.asyncTreeCom .u-form-control:focus {
  border-color: #d0d0d0;
  outline: 0;
}
.asyncTreeCom .NC_syncTreeSearch {
  position: relative;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
}
.asyncTreeCom .NC_syncTreeSearch .syncTreeSearchIcon {
  position: absolute;
  right: 10px;
  top: 7px;
  color: #878b94;
  cursor: pointer;
}
.asyncTreeCom .asynctree-area {
  padding-bottom: 10px;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  overflow: auto;
  width: 100%;
}
.asyncTreeCom .asynctree-area .u-tree .node-item:only-child > ul::before {
  border-left: none;
}
.asyncTreeCom .asynctree-area .u-tree-child-tree-open {
  position: relative;
  padding-left: 23px;
}
.asyncTreeCom .asynctree-area .u-tree-child-tree-open > li:first-child::after {
  position: absolute;
  left: 8px;
  top: -10px;
  content: "";
  display: inline-block;
  width: 1px;
  height: 20px;
  /* background: red; */
  border-left: 1px dashed #d0d0d0;
}
.asyncTreeCom .asynctree-area .u-tree-child-tree-open::before {
  position: absolute;
  left: 8px;
  top: -10px;
  content: "";
  display: inline-block;
  width: 1px;
  height: 100%;
  /* background: red; */
  border-left: 1px dashed #d0d0d0;
}
.asyncTreeCom .asynctree-area .node-item + .node-item::after {
  position: absolute;
  left: 8px;
  top: -15px;
  content: "";
  display: inline-block;
  width: 1px;
  height: 100%;
  /* background: red; */
  border-left: 1px dashed #d0d0d0;
}
.asyncTreeCom .asynctree-area .node-item {
  position: relative;
  z-index: 2;
}
.asyncTreeCom .asynctree-area .node-item .u-tree-switcher {
  position: relative;
  z-index: 2;
}
.asyncTreeCom .asynctree-area .node-item .u-tree-switcher::before {
  position: absolute;
  left: 11px;
  top: 8px;
  content: "";
  display: inline-block;
  width: 12px;
  border-top: 1px dashed #d0d0d0;
}
.asyncTreeCom .asynctree-area .node-item .u-tree-checkbox .u-tree-checkbox-inner {
  left: 8px;
}
.asyncTreeCom .asynctree-area .node-item .u-tree-node-content-wrapper {
  margin-left: 6px;
}
.asyncTreeCom .NC_iconBox {
  opacity: 0;
}
.asyncTreeCom .NC_iconBox .icon:first-child {
  margin-left: 20px;
}
.asyncTreeCom .title-con:hover .NC_iconBox {
  opacity: 1;
}
.asyncTreeCom .edit-icon,
.asyncTreeCom .add-icon,
.asyncTreeCom .delete-icon {
  line-height: 30px;
  margin-right: 5px;
  font-size: 14px;
}
.asyncTreeCom .u-tree {
  padding: 0;
  margin-top: 20px;
}
.asyncTreeCom .title-middle {
  line-height: 30px;
  margin-left: 6px;
}
.asyncTreeCom .u-tree li {
  line-height: 30px;
}
.asyncTreeCom .u-tree-searchable-filter {
  color: #f50;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.asyncTreeCom .addRoot {
  color: #fff;
  cursor: pointer;
  width: 80px;
  height: 30px;
  border-radius: 4px;
  line-height: 30px;
  background: #f34b4b;
  text-align: center;
  margin-bottom: 5px;
  font-size: 12px;
}
.asyncTreeCom .active-nodisable {
  background: #f4f5f7;
  width: 100%;
  color: #e14c46;
}
.asyncTreeCom .active-nodisable .title-middle {
  color: #e14c46;
}
.asyncTreeCom .title-con {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  overflow: hidden;
  min-width: 150px;
}
.asyncTreeCom .title-con input {
  float: left;
}
.asyncTreeCom .node_right_opt {
  position: absolute;
  right: 5px;
}
.tree-swich {
  color: #757f8c;
}
.tree-wenjian {
  color: #f2b224;
}
.tree-dian {
  content: ".";
  display: inline-block;
  width: 5px;
  height: 5px;
  background: #555;
  border-radius: 50%;
  margin-right: 5px;
  margin-left: 5px;
}
