.graphic-app {
  width: 100%;
  height: 100%;
}
.graphic-app .no-data-placeholder {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding-top: 150px;
}
.graphic-app .no-data-placeholder .no-data {
  background: url(../../../public/static/images/nodata.png) center center / contain no-repeat;
  display: inline-block;
  width: 26px;
  height: 26px;
}
.graphic-app .no-data-placeholder .no-data-title {
  padding-left: 8px;
  color: #aeaeae;
}
