.anchorTabs {
  display: inline-block;
  text-align: center;
  position: relative;
}
.anchorTabs .anchorTabsItem {
  text-align: center;
  float: left;
  margin-right: 43px;
  font-size: 15px;
  color: #666;
  cursor: pointer;
}
.anchorTabs .anchorTabsItem.active {
  color: #ff0000;
}
.anchorTabs .scrollBar {
  position: absolute;
  height: 3px;
  width: 24px;
  border-radius: 1px;
  bottom: 2px;
  left: 20px;
  background-color: #ff0000;
}
