/* FormGroup */
/*  Navlayout  */
.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  -webkit-transition-property: height, visibility;
  transition-property: height, visibility;
  -webkit-transition-duration: 0.35s;
  transition-duration: 0.35s;
  -webkit-transition-timing-function: ease;
  transition-timing-function: ease;
}
.collapse {
  display: none;
}
.collapse.in {
  display: block;
}
.u-panel {
  line-height: 1.57142857;
  color: #757575;
  position: relative;
  margin-bottom: 20px;
  -webkit-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.1);
  background-color: #fff;
  border: 1px solid transparent;
  border-radius: 3px;
}
.u-panel .u-panel-heading {
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  position: relative;
  padding: 10px 15px;
  border-bottom: 1px solid transparent;
}
.u-panel h4,
.u-panel h3 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
  line-height: 1.2;
  color: #424242;
  font-size: 18px;
  text-shadow: rgba(0, 0, 0, 0.15) 0 0 1px;
}
.u-panel .u-panel-footer {
  padding: 10px 15px;
  background-color: #f5f5f5;
  border-top: 1px solid #ddd;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
}
.u-panel .u-panel-title {
  margin-top: 0;
  margin-bottom: 0;
  display: block;
  font-size: 18px;
  color: #424242;
}
.u-panel .u-panel-title .small {
  color: #757575;
}
.u-panel .u-panel-body {
  padding: 15px 15px;
  position: relative;
}
.u-panel .u-panel-body .u-panel-copy {
  position: absolute;
  right: 25px;
  top: 30px;
  color: #fff;
  font-size: 20px;
  padding: 2px 8px;
  margin: 8px;
  border-radius: 4px;
  cursor: pointer;
}
.u-panel .u-panel-body .u-panel-copy:hover {
  color: #a8a7a7;
}
.u-panel-default {
  border-color: #ddd;
}
.u-panel-default .u-panel-heading {
  background-color: #f5f5f5;
  color: #333;
}
.u-panel-default .u-panel-footer {
  border-top: 1px solid transparent;
}
.u-panel-bordered {
  border-color: #ddd;
}
.u-panel-bordered .u-panel-heading {
  border-bottom: 1px solid #ddd;
}
.u-panel-bordered .u-panel-footer {
  border-top: 1px solid #ddd;
  background-color: #fff;
}
.u-panel > :not(.panel-loading):not(.collapsing) {
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}
.u-button-group-vertical > .u-button-group:after,
.u-button-group-vertical > .u-button-group:before,
.u-button-toolbar:after,
.u-button-toolbar:before,
.clearfix:after,
.clearfix:before,
.u-container-fluid:after,
.container-fluid:before,
.u-container:after,
.u-container:before,
.dl-horizontal dd:after,
.dl-horizontal dd:before,
.form-horizontal .form-group:after,
.form-horizontal .form-group:before,
.u-modal-footer:after,
.u-modal-footer:before,
.u-modal-header:after,
.u-modal-header:before,
.nav:after,
.nav:before,
.navbar-collapse:after,
.navbar-collapse:before,
.navbar-header:after,
.navbar-header:before,
.navbar:after,
.navbar:before,
.pager:after,
.pager:before,
.u-panel-body:after,
.u-panel-body:before,
.row:after,
.row:before {
  display: table;
  content: " ";
}
.u-panel-body .h1:first-child,
.u-panel-body .h2:first-child,
.u-panel-body .h3:first-child,
.u-panel-body .h4:first-child,
.u-panel-body .h5:first-child,
.u-panel-body .h6:first-child,
.u-panel-body h1:first-child,
.u-panel-body h2:first-child,
.u-panel-body h3:first-child,
.u-panel-body h4:first-child,
.u-panel-body h5:first-child,
.u-panel-body h6:first-child {
  margin-top: 0;
}
.u-panel-group {
  margin-bottom: 20px;
}
.u-panel-group .u-panel {
  margin-bottom: 0;
  border-radius: 4px;
}
.u-panel-group .u-panel + .u-panel {
  margin-top: 5px;
}
.u-panel-group .u-panel-heading {
  border-bottom: 0;
  cursor: pointer;
}
.u-panel-group .u-panel-heading + .u-panel-collapse > .list-group,
.u-panel-group .u-panel-group .u-panel-heading + .u-panel-collapse > .u-panel-body {
  border-top: 1px solid #ddd;
}
