.cardPagination-lightapp-component {
  margin-left: 6px;
}
.cardPagination-lightapp-component .cardPagination-lightapp-component-wrap {
  display: inline-block;
}
.cardPagination-lightapp-component .cardPagination-lightapp-component-wrap .cardPaginationBtn {
  display: inline-block;
  background-color: #ffffff;
  height: 30px;
  width: 30px;
  line-height: 30px;
  text-align: center;
  font-size: 13px;
  min-width: 10px;
  padding: 0px;
  border: 1px solid #d0d0d0;
  -webkit-box-shadow: 0 1px 1px 0 rgba(209, 209, 209, 0.5);
          box-shadow: 0 1px 1px 0 rgba(209, 209, 209, 0.5);
  color: inherit;
  border-right: none;
}
.cardPagination-lightapp-component .cardPagination-lightapp-component-wrap .cardPaginationBtn .iconfont {
  font-size: 13px;
}
.cardPagination-lightapp-component .cardPagination-lightapp-component-wrap .cardPaginationBtn.first-item {
  border-radius: 3px 0 0 3px;
}
.cardPagination-lightapp-component .cardPagination-lightapp-component-wrap .cardPaginationBtn.last-item {
  border-radius: 0 3px 3px 0;
  border-right: 1px solid #d0d0d0;
}
.cardPagination-lightapp-component .cardPagination-lightapp-component-wrap .cardPaginationBtn.last-item,
.cardPagination-lightapp-component .cardPagination-lightapp-component-wrap .cardPaginationBtn.second-item,
.cardPagination-lightapp-component .cardPagination-lightapp-component-wrap .cardPaginationBtn.third-item {
  border-left-color: #d0d0d0;
}
.cardPagination-lightapp-component .cardPagination-lightapp-component-wrap .cardPaginationBtn:hover {
  color: inherit;
  border-color: #e14c46;
  cursor: pointer;
}
.cardPagination-lightapp-component .cardPagination-lightapp-component-wrap .cardPaginationBtn:hover + .cardPaginationBtn {
  border-left-color: #e14c46;
}
.cardPagination-lightapp-component .cardPagination-lightapp-component-wrap .cardPaginationBtn.disabled,
.cardPagination-lightapp-component .cardPagination-lightapp-component-wrap .cardPaginationBtn[disabled] {
  color: #ddd;
  border-color: #d0d0d0;
}
.cardPagination-lightapp-component .cardPagination-lightapp-component-wrap .cardPaginationBtn[disabled]:hover {
  border-color: #d0d0d0;
  color: #ddd;
  cursor: not-allowed;
}
.cardPagination-lightapp-component .cardPagination-lightapp-component-wrap .cardPaginationBtn[disabled]:hover + .cardPaginationBtn {
  border-left-color: #d0d0d0;
}
.cardPagination-lightapp-component.hide {
  display: none;
}
.cardPagination-lightapp-component.show {
  display: block;
}
