@import "../../static/font/iconfont.css";
.ui-nc-header-warp {
  color: #e3e7ed;
}
.nc-rangepicker-wrapper .u-input-group-btn i {
  cursor: pointer;
}
.nc-rangepicker-wrapper .u-input-group.simple span.u-input-group-bt {
  pointer-events: auto;
}
.nc-rangepicker-calendar .rc-calendar {
  color: #212121;
  background-color: #fff;
  border: 1px solid #a5adba;
  -webkit-box-shadow: 0 1px 5px #ccc;
          box-shadow: 0 1px 5px #ccc;
}
.nc-rangepicker-calendar .rc-calendar-input {
  background-color: #fff;
  color: #212121;
}
.nc-rangepicker-calendar .rc-calendar-date {
  color: #212121;
}
.nc-rangepicker-calendar .rc-calendar-year-select,
.nc-rangepicker-calendar .rc-calendar-month-select,
.nc-rangepicker-calendar .rc-calendar-day-select {
  color: #212121;
}
.nc-rangepicker-calendar .rc-calendar .rc-calendar-footer {
  border-top: 1px solid #ccc;
  border-top: none;
}
.nc-rangepicker-calendar .rc-calendar .rc-calendar-footer .rc-calendar-footer-btn {
  border-top: 1px solid #dee0e3;
}
.nc-rangepicker-calendar .rc-calendar-header {
  border-bottom: 1px solid #ccc;
}
.nc-rangepicker-calendar .rc-calendar-input-wrap {
  border-bottom: 1px solid #e9e9e9;
}
.nc-rangepicker-calendar .rc-calendar-year-panel {
  background-color: #fff;
}
.nc-rangepicker-calendar .rc-calendar-decade-panel {
  background-color: #fff;
}
.nc-rangepicker-calendar .rc-calendar-decade-panel-decade {
  color: #212121;
}
.nc-rangepicker-calendar .rc-calendar-year-panel-year {
  color: #212121;
}
.nc-rangepicker-calendar .rc-calendar-month-panel {
  background-color: #fff;
}
.nc-rangepicker-calendar .rc-calendar-month-panel-cell .rc-calendar-month-panel-month {
  color: #212121;
}
.u-form-control-wrapper {
  display: inline-block;
  position: relative;
}
.u-form-control-wrapper .calendar-picker.nc-input {
  height: 100%;
}
.u-form-control-wrapper .calendar-picker.nc-input .u-form-control {
  height: 30px !important;
}
.u-form-control-wrapper .calendar-picker.nc-input .u-form-control:hover {
  border-color: #8cc6ff;
}
.u-form-control-wrapper .calendar-picker.nc-input .u-form-control:focus {
  border-color: #007ace;
}
.u-form-control-wrapper .calendar-picker.nc-input .u-form-control::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #ccc;
}
.u-form-control-wrapper .calendar-picker.nc-input .u-form-control:-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #ccc;
}
.u-form-control-wrapper .calendar-picker.nc-input .u-form-control:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #ccc;
}
.u-form-control-wrapper .range-picker-required-tag {
  position: absolute;
  left: 2px;
  top: 55%;
  z-index: 3;
  -webkit-transform: translate3d(0, -50%, 0);
          transform: translate3d(0, -50%, 0);
  color: #ef0012;
  font-size: 13px;
}
.u-form-control-wrapper .u-input-group-btn i.iconfont {
  font-size: 14px;
  color: #999;
}
.u-form-control-wrapper .u-input-group-btn i:before {
  font-family: "iconfont";
}
.rc-calendar-picker {
  z-index: 300 !important;
}
.fun-plan-area {
  text-align: center;
  margin-top: 5px;
}
.fun-plan-area span {
  cursor: pointer;
  color: #0000ff;
}
.fun-plan-area span:hover {
  color: #ff0000;
}
.date-picker-mask {
  position: absolute;
  top: 0px;
  left: 10px;
  z-index: 2;
  color: #000;
}
