.bill-approve-modal {
  -webkit-box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  background: #fff;
  z-index: 190;
  position: absolute;
  top: 50%;
  left: 50%;
  max-height: 80%;
  width: 1200px;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}
.bill-approve-modal.hide-approve-detail {
  visibility: hidden;
}
.bill-approve-modal.show-approve-detail {
  display: block;
}
.bill-approve-modal .bill-approve-title {
  width: 100%;
  height: 40px;
  padding: 12px 20px 8px 20px;
  border-bottom: 1px solid #e9e9e9;
  text-align: left;
}
.bill-approve-modal .bill-approve-title-name {
  font-size: 14px;
}
.bill-approve-modal .bill-approve-title-close {
  float: right;
  cursor: pointer;
  font-size: 14px;
  margin-top: 3px;
}
.bill-approve-modal .bill-approve-detail {
  padding: 10px;
  min-height: 150px;
}
.bill-approve-modal-shade {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 180;
  background-color: #000;
  opacity: 0.3;
  overflow-x: hidden;
  overflow-y: auto;
}
