.u-drop {
  background: #f7f9fb;
  border: 1px solid #ebecf0;
  padding: 12px;
  color: #212121;
}
.u-drop.u-drop-horizontal {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}
.u-drag {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 4px;
  min-width: 100px;
  background: white;
  border-radius: 3px;
  border: 1px solid #a5adba;
  padding: 6px 0;
  font-size: 12px;
}
.u-drag:hover {
  background: #ebecf0;
  border: 1px solid #a5adba;
}
.u-drag:focus {
  outline: none;
}
