@import "../../static/font/iconfont.css";
.ui-nc-header-warp {
  color: #e3e7ed;
}
.bd-header {
  height: 50px;
  margin-top: 20px;
  border: 1px solid #e3e7ed;
  border-radius: 3px 3px 0 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.bd-header .bd-header-title {
  color: #333333;
  font-weight: 700;
  font-size: 16px;
}
.bd-header .bd-header-search {
  margin-right: 20px;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.bd-header .bd-header-search > div {
  width: auto;
  display: inline-block;
}
.bd-header .bd-header-search .u-form-control {
  border: 1px solid #e3e7ed;
}
.bd-header .bd-header-search .u-form-control-suffix .uf {
  display: none;
}
.bd-header .bd-header-search .u-input-suffix .uf {
  display: none;
}
.bd-header input::-webkit-input-placeholder {
  color: #cccccc;
}
.bd-header .searchbutton {
  position: absolute;
  right: 10px;
}
