.nc-dropdown.u-dropdown {
  z-index: 300;
}
.nc-dropdown.u-dropdown .u-dropdown-menu {
  background-color: #fff;
  -webkit-box-shadow: 0 1px 5px #a5adba;
          box-shadow: 0 1px 5px #a5adba;
}
.nc-dropdown.u-dropdown .u-dropdown-menu > .u-dropdown-menu-item {
  color: #555;
}
.nc-dropdown.u-dropdown .u-dropdown-menu > .u-dropdown-menu-item:hover,
.nc-dropdown.u-dropdown .u-dropdown-menu > .u-dropdown-menu-item-active,
.nc-dropdown.u-dropdown .u-dropdown-menu > .u-dropdown-menu-item-selected {
  color: #111;
  background-color: #ebecf0;
}
.nc-dropdown.u-dropdown .u-dropdown-menu > .u-dropdown-menu-item-divider {
  background-color: #e0e0e0;
}
