/* You can remove this page div in your website */
.nc-img-differ {
  position: absolute;
  background: #eee;
}
/* Our wrapper */
.img-differ-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
/* Our image information */
.img-differ-bottom,
.img-differ-middle,
.img-differ-bg {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-color: white;
  background-size: cover;
  background-position: center;
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
  overflow: hidden;
}
.img-differ-bottom > img,
.img-differ-middle > img,
.img-differ-bg > img {
  max-height: 100%;
  position: absolute;
  left: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  top: 50%;
}
.img-differ-scroller {
  width: 50px;
  position: absolute;
  left: 100px;
  background-color: #c7c7c7;
}
.img-differ-scroller-middle {
  margin-top: 25px;
}
.img-differ-scroller-top {
  margin-top: -25px;
}
.img-differ-scroller:hover {
  opacity: 1;
}
.img-differ-scrolling {
  pointer-events: none;
  opacity: 1;
}
.img-differ-scroller-thumb {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  padding: 7px;
}
.img-differ-scroller:before,
.img-differ-scroller:after {
  content: " ";
  display: block;
  width: 7px;
  height: 9999px;
  position: absolute;
  left: 50%;
  margin-left: -3.5px;
  z-index: 30;
  -webkit-transition: 0.1s;
  transition: 0.1s;
  -webkit-box-shadow: 3.5px 0px 7px rgba(100, 100, 100, 0.2);
          box-shadow: 3.5px 0px 7px rgba(100, 100, 100, 0.2);
}
.img-differ-scroller:before {
  top: 0;
}
.img-differ-scroller:after {
  bottom: 0;
}
/* If you want to cahnge the colors, make sure you change the fill in the svgs to match */
.img-differ-scroller-middle > .img-differ-scroller-thumb {
  border: 5px solid #c7c7c7;
}
.img-differ-scroller-middle:before,
.img-differ-scroller-middle:after {
  background: #c7c7c7;
}
.img-differ-scroller-top > .img-differ-scroller-thumb {
  border: 5px solid #ffab91;
}
.img-differ-scroller-top:before,
.img-differ-scroller-top:after {
  background: #ffab91;
}
