@import "../../static/font/iconfont.css";
.ui-nc-header-warp {
  color: #e3e7ed;
}
input.u-form-control {
  background: #fff;
  border: 1px solid #d5d6d9;
  color: #424242;
}
.u-form-control-wrapper {
  display: inline-block;
  position: relative;
  width: 100%;
  vertical-align: middle;
}
.u-form-control-wrapper .form-control-required-tag {
  position: absolute;
  left: 2px;
  top: 55%;
  z-index: 1;
  -webkit-transform: translate3d(0, -50%, 0);
          transform: translate3d(0, -50%, 0);
  color: #ef0012;
  font-size: 13px;
}
.u-form-control-wrapper:hover .icon-qingkong {
  display: inline-block;
}
.u-form-control-wrapper .u-form-control {
  background: #fff;
  color: #424242;
  height: 30px;
}
.nc-form-box {
  position: relative;
  display: inherit;
}
.u-form-control[disabled] {
  background: #f3f3f3 !important;
  color: #2b344b !important;
  border-color: #e0e0e0 !important;
}
.nc-input.u-form-control {
  border: 1px solid #d5d6d9;
  height: 30px;
  color: #424242;
  padding: 0 8px;
  border-radius: 3px !important;
  font-size: 13px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  vertical-align: top;
}
.nc-input.u-form-control:hover {
  border-color: #8cc6ff;
}
.nc-input.u-form-control:focus {
  border-color: #007ace;
}
.nc-input.u-form-control::-webkit-input-placeholder {
  color: #ccc;
}
.nc-input.u-form-control::-moz-placeholder {
  color: #ccc;
}
.nc-input.u-form-control::-ms-input-placeholder {
  color: #ccc;
}
.u-form-control-search-text {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  z-index: 2;
  right: 7px;
  color: rgba(0, 0, 0, 0.65);
}
.u-form-control-search-text .iconfont {
  padding: 0;
}
.u-form-control-search-text .icon-qingkong {
  display: none;
  color: rgba(0, 0, 0, 0.3);
}
.u-form-control-search-text .icon-qingkong:hover {
  color: rgba(0, 0, 0, 0.65);
}
