.nc-bill-excelimport {
  float: right;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
  padding-right: 10px;
}
.nc-bill-excelimport .excel-export-footer {
  overflow: hidden;
  background-color: #ddd;
  color: inherit;
}
.excel-export-modal .u-modal-body {
  padding: 16px 16px 62px;
}
.excel-export-modal .excel-export-footer {
  background-color: #ececec;
  color: inherit;
  overflow: hidden;
  padding: 8px 6px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 500;
}
.excel-export-modal .lf {
  float: left;
}
.excel-export-modal .rt {
  float: right;
}
