@import "../../static/font/iconfont.css";
.ui-nc-header-warp {
  color: #e3e7ed;
}
.u-form-control-wrapper {
  display: inline-block;
  position: relative;
}
.u-form-control-wrapper .u-time-picker .u-time-picker-input {
  background: #fff;
  color: #212121;
}
.u-form-control-wrapper .time-picker-required-tag {
  position: absolute;
  left: 2px;
  top: 55%;
  z-index: 1;
  -webkit-transform: translate3d(0, -50%, 0);
          transform: translate3d(0, -50%, 0);
  color: #ef0012;
  font-size: 13px;
}
.u-form-control-wrapper input.u-time-picker-input {
  height: 30px;
  border-color: #d5d6d9;
}
.u-form-control-wrapper input.u-time-picker-input:hover {
  border-color: #8cc6ff;
}
.u-form-control-wrapper input.u-time-picker-input:focus {
  border-color: #007ace;
  outline: none;
}
.u-form-control-wrapper .u-time-picker {
  width: 100%;
}
.u-form-control-wrapper .u-time-picker .u-time-picker-input {
  border-radius: 3px;
}
.u-form-control-wrapper .u-time-picker .u-time-picker-input[disabled] {
  background: #F3F3F3;
  border-color: #e0e0e0;
}
.u-form-control-wrapper .u-time-picker i.uf {
  right: 6px;
  top: 7px;
}
.rc-time-picker-panel-select li {
  text-align: center;
  padding: 0;
}
.u-time-picker-panel-clear-btn-icon {
  width: 26px;
}
.u-time-picker-panel-clear-btn-icon:after {
  content: "\e6db";
  font-family: iconfont;
  color: #007ace;
  width: 26px;
}
.u-time-picker-panel-clear-btn-icon:after:hover {
  color: #005895;
}
.nc-time-picker-panel.u-time-picker-panel input {
  background-color: #fff;
  color: #000;
}
.nc-time-picker-panel.u-time-picker-panel .u-time-picker-panel-inner {
  background-color: #fff;
  border: 1px solid #ccc;
  -webkit-box-shadow: 0 1px 5px #ccc;
          box-shadow: 0 1px 5px #ccc;
}
.nc-time-picker-panel.u-time-picker-panel .u-time-picker-panel-select li {
  color: #212121;
}
.nc-time-picker-panel.u-time-picker-panel .u-time-picker-panel-select li:hover {
  background: #ebecf0;
}
.nc-time-picker-panel.u-time-picker-panel li.u-time-picker-panel-select-option-selected {
  background: #fff7e7;
  color: #f53c32;
}
.nc-time-picker-panel.u-time-picker-panel .u-time-picker-panel-input {
  border: 1px solid transparent;
}
.nc-time-picker-panel.u-time-picker-panel .u-time-picker-panel-input-wrap {
  border-bottom: 1px solid #ccc;
}
.nc-time-picker-panel.u-time-picker-panel .u-time-picker-panel-select {
  border: 1px solid #ccc;
}
