@import "../../static/font/iconfont.css";
.ui-nc-header-warp {
  color: #e3e7ed;
}
.nc-pagination .u-pagination-list > li > a,
.nc-pagination .u-pagination-list > li > span {
  background: #fff;
  color: #555;
  border-color: #d0d0d0;
  border-left-color: #d0d0d0;
}
.nc-pagination .u-pagination-list > li > a:hover,
.nc-pagination .u-pagination-list > li > span:hover,
.nc-pagination .u-pagination-list > li > a:focus,
.nc-pagination .u-pagination-list > li > span:focus {
  color: #e14c46;
  background-color: #eee;
  border-color: #d0d0d0;
}
.nc-pagination .u-pagination-list > li:last-child > a,
.nc-pagination .u-pagination-list > li:last-child > span {
  border-right-color: #d0d0d0;
}
.nc-pagination .u-pagination-list > li.iconBtn > a {
  padding: 6px 11px;
  font-size: 13px;
}
.nc-pagination .u-pagination-list > .disabled > a,
.nc-pagination .u-pagination-list > .disabled > span,
.nc-pagination .u-pagination-list > .disabled > a:hover,
.nc-pagination .u-pagination-list > .disabled > span:hover,
.nc-pagination .u-pagination-list > .disabled > a:focus,
.nc-pagination .u-pagination-list > .disabled > span:focus {
  color: #777;
  background-color: #fff;
  border-color: #ddd;
}
.NC_PaginationStyle.nc-pagination .u-pagination-list li {
  box-sizing: border-box !important;
  margin-left: 8px;
  min-width: 28px;
  width: auto;
  height: 28px;
  border-radius: 4px;
  border: 1px solid #d0d0d0;
  text-align: center;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  line-height: 26px;
  color: #555;
  background: #fff;
  cursor: pointer;
}
.NC_PaginationStyle.nc-pagination .u-pagination-list li:hover a {
  color: #e14c46 !important;
}
.NC_PaginationStyle.nc-pagination .u-pagination-list li:disabled a:hover {
  color: #555;
}
.NC_PaginationStyle.nc-pagination .u-pagination-list li.active {
  border-color: #e14c46;
}
.NC_PaginationStyle.nc-pagination .u-pagination-list li.active a {
  color: #e14c46 !important;
}
.NC_PaginationStyle.nc-pagination .u-pagination-list li a {
  margin: 0 !important;
  padding: 0 !important;
  border: none !important;
  font-size: 12px;
  -webkit-box-sizing: border-box;
  box-sizing: content-box;
  text-align: center;
  background-color: #fff !important;
  display: inline-block;
  min-width: 26px;
  width: auto;
  height: 26px;
  float: left;
  margin-top: 1px;
  line-height: 26px !important;
  border-radius: 4px;
  color: #555;
}
.NC_PaginationStyle.nc-pagination .u-pagination-list li a span {
  height: 26px;
  line-height: 24px;
  display: inline-block;
  vertical-align: top;
}
.NC_PaginationStyle.nc-pagination .u-pagination-list li a:hover {
  color: #e14c46 !important;
}
.pagination-hotkeys-wrapper {
  outline: none;
}
