.sidebox .sidebox-mask {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 230;
}
.sidebox .sidebox-content {
  background-color: #fff;
  position: fixed;
  width: 800px;
  height: calc(100% - 8px);
  right: 0px;
  top: 8px;
  z-index: 230;
}
.sidebox .sidebox-content .sidebox-header {
  border-bottom: 1px solid #e9e9e9;
  line-height: 54px;
  height: 54px;
  padding: 0px 20px;
  background-color: #fff;
}
.sidebox .sidebox-content .sidebox-header .sidebox-title {
  font-weight: bold;
  font-size: 14px;
  color: #111;
}
.sidebox .sidebox-content .sidebox-header .goto-opre {
  float: right;
}
.sidebox .sidebox-content .sidebox-header .prev,
.sidebox .sidebox-content .sidebox-header .next,
.sidebox .sidebox-content .sidebox-header .first,
.sidebox .sidebox-content .sidebox-header .end {
  display: inline-block;
  cursor: pointer;
  font-size: 16px;
  width: 30px;
  line-height: 30px;
  height: 30px;
  border: 1px solid #d0d0d0;
  background: #fff;
  color: #555;
}
.sidebox .sidebox-content .sidebox-header .prev:hover,
.sidebox .sidebox-content .sidebox-header .next:hover,
.sidebox .sidebox-content .sidebox-header .first:hover,
.sidebox .sidebox-content .sidebox-header .end:hover {
  color: #ff0000;
}
.sidebox .sidebox-content .sidebox-header .prev.cannot-click,
.sidebox .sidebox-content .sidebox-header .next.cannot-click,
.sidebox .sidebox-content .sidebox-header .first.cannot-click,
.sidebox .sidebox-content .sidebox-header .end.cannot-click {
  color: #ccc;
}
.sidebox .sidebox-content .sidebox-header .prev.cannot-click:hover,
.sidebox .sidebox-content .sidebox-header .next.cannot-click:hover,
.sidebox .sidebox-content .sidebox-header .first.cannot-click:hover,
.sidebox .sidebox-content .sidebox-header .end.cannot-click:hover {
  color: #ccc;
  cursor: not-allowed;
}
.sidebox .sidebox-content .sidebox-header .first {
  padding-left: 8px;
  border-radius: 3px 0px 0px 3px;
}
.sidebox .sidebox-content .sidebox-header .next {
  border-left: none;
  padding-left: 8px;
}
.sidebox .sidebox-content .sidebox-header .prev {
  border-left: none;
  padding-left: 8px;
}
.sidebox .sidebox-content .sidebox-header .end {
  padding-left: 8px;
  border-left: none;
  border-radius: 0px 3px 3px 0px;
}
.sidebox .sidebox-content .sidebox-header .icon-you {
  display: inline-block;
  cursor: pointer;
  border: 1px solid #d0d0d0;
  line-height: 30px;
  width: 30px;
  height: 30px;
  text-align: center;
  background: #fff;
  margin-left: 17px;
  border-radius: 3px;
  color: #111;
}
.sidebox .sidebox-content .sidebox-header .icon-you:hover {
  color: #ff0000;
}
.sidebox .sidebox-content .sidebox-body {
  max-height: 100%;
  width: 100%;
  overflow-y: auto;
}
