@import "../../static/font/iconfont.css";
.ui-nc-header-warp {
  color: #e3e7ed;
}
.u-form-control-wrapper {
  display: inline-block;
  position: relative;
  width: 100%;
  background-color: #fff;
}
.u-form-control-wrapper .input-required-tag {
  position: absolute;
  left: 2px;
  top: 55%;
  z-index: 1;
  -webkit-transform: translate3d(0, -50%, 0);
          transform: translate3d(0, -50%, 0);
  color: #ef0012;
  font-size: 13px;
}
.u-form-control-wrapper .u-form-control.nc-input {
  border-color: #d5d6d9;
}
.u-form-control-wrapper .u-form-control:hover {
  border-color: #8cc6ff;
}
.u-form-control-wrapper .u-form-control:focus {
  border-color: #007ace;
}
.nc-input.u-form-control {
  border-color: #d5d6d9;
  height: 30px;
  color: #2B344B;
  padding: 0 8px;
  border-radius: 3px;
  font-size: 13px;
}
.nc-input.u-form-control:focus {
  border-color: #007ace;
}
