.import-modal.u-modal {
  width: 600px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
.import-modal.u-modal .u-modal-header .u-close {
  display: none;
}
.import-modal.u-modal .uf-close {
  position: absolute;
  top: -27px;
  right: 10px;
  z-index: 10;
  cursor: pointer;
  font-size: 13px;
}
.import-modal.u-modal .u-modal-body {
  height: 120px;
  overflow: visible !important;
}
.import-modal.u-modal .deal-success {
  position: absolute;
  left: 42px;
  top: 20px;
  font-size: 28px;
  color: #66b83f;
}
.import-modal.u-modal .tips-txt {
  margin-left: 68px;
  display: block;
  margin-top: 6px;
  font-weight: normal;
  font-size: 16px;
}
.import-modal.u-modal .go-import-detail {
  margin-top: 9px;
  cursor: pointer;
  font-size: 13px;
  color: #007ace;
  border-bottom: 1px solid #007ace;
  padding-bottom: 4px;
}
.import-modal.u-modal .u-progress {
  margin: 10px 0 0 68px;
}
.toastvr-zijinyun-project .go-import-detail {
  display: inline-block;
  margin-top: 9px;
  cursor: pointer;
  font-size: 13px;
  color: #007ace;
  border-bottom: 1px solid #007ace;
  padding-bottom: 2px;
}
.process-item-contain {
  height: 40px;
}
.process-item-contain .tips-txt {
  width: 226px;
  margin-bottom: 16px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
