/*  Navlayout  */
.u-breadcrumb {
  padding: 8px 15px;
  margin-bottom: 20px;
  list-style: none;
  background-color: #f5f5f5;
  border-radius: 4px;
}
.u-breadcrumb li {
  display: inline-block;
  color: #00bcd4;
}
.u-breadcrumb a {
  color: #00bcd4;
  text-decoration: none;
}
.u-breadcrumb .active {
  color: #777;
}
.u-breadcrumb > li + li:before {
  content: "/\A0";
  padding: 0 5px;
  color: #ccc;
}
