.u-radio-group .u-radio .u-radio-label {
  color: rgba(0, 0, 0, 0.65);
}
.u-radio-group .u-radio {
  width: 16px;
  height: 16px;
}
.u-radio-group .u-radio .u-radio-label::before {
  -webkit-box-shadow: inset 0 0 0 10px #fff;
  box-shadow: inset 0 0 0 10px #fff;
  border-color: #c4cdd5;
}
.u-radio-group .u-radio input[type="radio"] {
  display: inline-block;
}
.u-radio-group .u-radio.is-checked .u-radio-label::after {
  background-color: #e14c46;
}
.u-radio-group .u-radio.is-checked .u-radio-label::before {
  -webkit-box-shadow: inset 0 0 0 10px #fff;
  box-shadow: inset 0 0 0 10px #fff;
  border-color: #e14c46;
}
.u-radio-group .u-radio.is-checked input[type="radio"]:focus + .u-radio-label:before {
  border-color: #e14c46;
}
.u-radio-group .u-radio.is-checked.disabled .u-radio-label::before {
  border-color: #c4cdd5;
}
.u-radio-group .u-radio.is-checked.disabled .u-radio-label::after {
  background-color: #c4cdd5;
}
.u-radio-group .u-radio.disabled {
  color: #c4cdd5;
}
.u-radio-group .u-radio.disabled .u-radio-label {
  color: #474d54;
}
.u-radio-group .u-radio.disabled .u-radio-label::before {
  -webkit-box-shadow: inset 0 0 0 10px #e9eaeb;
  box-shadow: inset 0 0 0 10px #e9eaeb;
  border-color: #c4cdd5;
}
.u-radio-group .nc-radio.u-radio-button {
  background-color: #fff;
  color: #212121;
  border-color: #a5adba;
}
.u-radio-group .nc-radio.u-radio-button:last-child {
  border-right: 1px solid #a5adba;
}
.u-radio-group .nc-radio.u-radio-button:hover,
.u-radio-group .nc-radio.u-radio-button.is-checked {
  background: #ebecf0;
}
.u-radio-group .nc-radio.u-radio-button:focus,
.u-radio-group .nc-radio.u-radio-button.focus,
.u-radio-group .nc-radio.u-radio-button:active,
.u-radio-group .nc-radio.u-radio-button.active,
.u-radio-group .nc-radio.u-radio-button.is-checked {
  border-color: #f53c32;
  background: #f53c32;
  color: #fff;
}
