.dragWidthCom {
  width: 100%;
  height: 100%;
  background-color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.dragWidthCom .dragWidthCom_left {
  min-width: 50px;
  height: 100%;
  overflow: auto;
}
.dragWidthCom .dragWidthCom_line {
  width: 1px;
  background-color: #d0d0d0;
  position: relative;
  cursor: col-resize;
}
.dragWidthCom .dragWidthCom_line .dragWidthCom_lines {
  width: 10px;
  height: 100%;
  cursor: col-resize;
  position: relative;
  z-index: 20;
  left: -5px;
  float: left;
  background-color: transparent;
}
.dragWidthCom .dragWidthCom_right {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 100%;
  overflow: auto;
  position: relative;
}
.dragWidthCom .dragWidthCom_mask {
  position: absolute;
  z-index: 9999;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: none;
}
