.toggle-view-btn {
  width: 30px;
  height: 30px;
  background: #fff;
  border: 1px solid #d0d0d0;
  color: #111;
  border-radius: 3px;
  position: relative;
  cursor: pointer;
  margin-left: 6px;
}
.toggle-view-btn:hover {
  border-color: #e14c46;
  color: #e14c46;
}
.toggle-view-btn:before {
  position: absolute;
  top: 6px;
  left: 6px;
}
