.approve-btns {
  display: inline-block;
}
.approve-btns .btns .yujing_sign,
.approve-btns .btns .bohui_sign {
  margin-left: 5px;
  font-size: 12px;
}
.approve-btns .btns .bohui_sign {
  color: #7cc6f9;
  border: 1px solid #7cc6f9;
}
.approve-btns .btns .yujing_sign {
  color: #f78c66;
  border: 1px solid #f78c66;
}
.approve-btns .btns .user-refer {
  display: inline-block;
}
.approve-btns .btns .user-refer .refer-wrapper {
  display: inline !important;
}
.approve-btns .btns .button-second {
  background: #e4e4e4;
  color: #555;
}
.approve-btns .btns .reject-btns {
  margin-left: 10px;
  display: inline-block;
  border-radius: 4px;
}
.approve-btns .btns .reject-btns .reject-btn {
  margin-left: 0;
  min-width: 50px;
  height: 30px;
  padding-right: 5px;
  border-right: none;
  border-radius: 3px 0px 0px 3px;
}
.approve-btns .btns .reject-btns .reject-list {
  display: inline-block;
  vertical-align: top;
  width: 20px;
  border: 1px solid #d0d0d0;
  margin-left: 0;
  padding: 0;
  min-width: 0;
  position: relative;
  background: #fff;
  border-radius: 0px 3px 3px 0px;
  height: 30px;
  cursor: pointer;
}
.approve-btns .btns .reject-btns .reject-list:before {
  position: absolute;
  top: 10px;
  right: 3px;
  font-size: 13px;
}
.approve-btns .reject-detail {
  -webkit-box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  background: #fff;
  z-index: 500;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}
.approve-btns .reject-detail .u-tabs-bar {
  background-color: #f3f3f3 !important;
}
.approve-btns .reject-detail .flow-list-info-contain {
  margin: 6px auto;
  width: 99%;
  height: 200px;
  overflow: hidden;
  border: 1px solid #d0d0d0;
}
.approve-btns .reject-detail.hide-reject-detail {
  visibility: hidden;
}
.approve-btns .reject-detail.show-reject-detail {
  display: block;
}
.approve-btns .reject-detail .title {
  padding: 0 10px;
  width: 100%;
  height: 40px;
  line-height: 40px;
  overflow: hidden;
  background-color: #f3f3f3;
  border-bottom: 1px solid #e9e9e9;
}
.approve-btns .reject-detail .title .close {
  float: right;
  cursor: pointer;
}
.approve-btns .reject-detail .title .close:before {
  color: #757f8c;
  font-size: 14px;
}
.approve-btns .reject-detail .title .name {
  padding-right: 6px;
  float: left;
  color: #474d54;
  font-size: 14px;
  font-weight: 600;
  position: relative;
}
.approve-btns .reject-detail .title .name::before {
  display: block;
  content: " ";
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  right: 0;
  height: 13px;
  width: 2px;
  background-color: #666;
}
.approve-btns .reject-detail .detail {
  max-width: 800px;
}
.approve-btns .reject-detail .detail .img {
  text-align: center;
}
.approve-btns .reject-detail .detail .hide-detail-img {
  display: none;
}
.approve-btns .reject-detail .detail .detail-btn {
  position: absolute;
  top: 60px;
  right: 20px;
  z-index: 1;
}
.approve-btns .reject-detail .detail .reject-dagre.dagre-area {
  width: 100%;
}
.approve-btns .reject-detail .detail .reject-dagre.dagre-area .dagre-extract {
  position: absolute;
  display: none;
  top: 15px;
  right: 100px;
}
.approve-btns .reject-detail .footer {
  width: 100%;
  height: 45px;
  line-height: 45px;
  padding: 0 20px;
  text-align: right;
}
.approve-btns .reject-detail-shade {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  background-color: #000;
  opacity: 0.3;
  overflow-x: hidden;
  overflow-y: auto;
}
.reject-menu {
  font-size: 13px;
  color: #474d54;
}
.reject-menu .u-dropdown-menu-item {
  line-height: 25px !important;
  height: 25px !important;
  width: 79px;
  text-align: center;
  margin-top: 0;
}
.reject-menu .u-dropdown-menu-item:first-child {
  margin-top: 0;
}
.reject-menu .u-dropdown-menu-item.menu-item {
  cursor: pointer;
}
.dagrechart-wrapper {
  max-height: 200px;
}
.approve-img {
  max-height: 300px;
}
.approve-warning-modal .u-modal-body {
  overflow: hidden;
}
.rejectMode {
  font-size: 10px;
  color: rgba(0, 0, 0, 0.65);
  margin-right: 15px;
}
.rejectMode span {
  font-size: 10px;
}
.rejectMode .u-radio-group {
  margin-left: 12px;
}
.rejectMode .u-radio-group .u-radio .u-radio-label {
  font-size: 10px;
  padding-left: 18px;
}
.rejectMode .u-radio-group .u-radio .u-radio-label:before {
  width: 15px;
  height: 15px;
  top: 3px;
}
.rejectMode .u-radio-group .u-radio .u-radio-label:after {
  height: 7px;
  width: 7px;
  border-radius: 7px;
  top: 7px;
  left: -1px;
}
.rejectMode .icon-bangzhutishi {
  color: #d7332c;
  margin-left: 5px;
  font-size: 15px;
}
