.input-number-wrapper {
  display: inline-block;
}
.input-number-wrapper .u-form-control-wrapper {
  display: inline-block;
  position: relative;
  width: 100%;
}
.input-number-wrapper .u-form-control-wrapper .input-number-required-tag {
  position: absolute;
  left: 25px;
  top: 55%;
  z-index: 100;
  color: #ef0012;
  font-size: 13px;
}
.input-number-wrapper .u-input-number {
  border: 1px solid #a5adba;
}
.input-number-wrapper .u-input-number.u-input-group > span:first-child {
  border-right: 1px solid #a5adba;
}
.input-number-wrapper .u-input-number.u-input-group > span:last-child {
  border-left: 1px solid #a5adba;
}
.input-number-wrapper .u-input-number.u-input-group > span {
  background: #fff;
}
.input-number-wrapper .u-input-number.u-input-group.simple .u-form-control.sm + .u-input-group-btn {
  top: 0;
}
.input-number-wrapper .u-input-group-addon {
  color: #505f79;
}
